<template>
  <div>
    <div class="cms-form-layout">
      <!-- <cms-prompt collapse='true'>
       <li> 用户关注公众号时，发送的欢迎信息。</li>
      </cms-prompt> -->
      <section class="cms-batch">
        <cms-button icon="add" type="success" size="small" @click="$routerLink('/wechatKeyword/save','save')">新增</cms-button>
        <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
      </section>
      <div style="margin-bottom:10px;margin-left:8px">
        <el-radio-group v-model="radio1" size="small" @change="changeItem(radio1)">
          <el-radio-button @change="changeItem('')" label="全部"></el-radio-button>
          <el-radio-button @change="changeItem('text')" label="回复文本消息"></el-radio-button>
          <el-radio-button @change="changeItem('image')" label="回复图片消息"></el-radio-button>
          <el-radio-button @change="changeItem('music')" label="回复语音消息"></el-radio-button>
          <el-radio-button @change="changeItem('video')" label="回复视频消息"></el-radio-button>
          <el-radio-button @change="changeItem('voice')" label="回复音乐消息"></el-radio-button>
          <el-radio-button @change="changeItem('news')" label="回复图文消息"></el-radio-button>
        </el-radio-group>
      </div>
      <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
        <el-table-column type="selection" width="46" fixed='left'></el-table-column>
        <el-table-column label="消息">
          <div slot-scope="scope" align="left">
            <div>
              <span>规则名：</span>{{scope.row.ruleName}}</div>
            <span>关键词：</span>
            <span v-for="(wordItem,wordIndex) in scope.row.wechatReplyKeywordList" :key="wordIndex" style="margin-left:5px">
              <el-tag size="mini" v-if="wordItem.wordkeyEq!==''">{{wordItem.wordkeyEq}}</el-tag>
              <el-tag size="mini" v-if="wordItem.wordkeyLike!==''">{{wordItem.wordkeyLike}}</el-tag>
            </span>
            <div>
              <span>回复类型：</span>
              <span v-if="scope.row.msgType ==='image'">图片</span>
              <span v-if="scope.row.msgType ==='text'">文本</span>
              <span v-if="scope.row.msgType ==='news'">图文</span>
              <span v-if="scope.row.msgType ==='voice'">语音</span>
              <span v-if="scope.row.msgType ==='video'">视频</span>
              <span v-if="scope.row.msgType ==='music'">音乐</span>
            </div>
          </div>
        </el-table-column>
        <el-table-column label="操作" width='175' fixed="right" align="center">
          <div slot-scope="scope" fixed="right" align="center">
            <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/wechatKeyword/keyWordEdit','update',{id:scope.row.id})">编辑</cms-button>
            <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import list from '@/mixins/list'
import { keywordPage, keywordDelete } from '@/api/wechat/autoResponse.js'
var defaultData = {}
export default {
  name: '',
  mixins: [form, list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      activeName: 'all',
      radio1: '全部',
      type: ''
    }
  },
  methods: {
    changeItem(label) {
      if (label === '全部') {
        this.type = ''
      } else if (label === '回复文本消息') {
        this.type = 'text'
      } else if (label === '回复图片消息') {
        this.type = 'image'
      } else if (label === '回复语音消息') {
        this.type = 'voice'
      } else if (label === '回复视频消息') {
        this.type = 'video'
      } else if (label === '回复音乐消息') {
        this.type = 'music'
      } else if (label === '回复图文消息') {
        this.type = 'news'
      }
      const autoParams = {
        page: 1,
        size: 10,
        msgType: this.type
      }
      this.getTableData(keywordPage, autoParams)
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(keywordDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(keywordDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(keywordPage, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(keywordPage, this.queryParams)
  }
}
</script>
<style  scoped>
.el-form-item .detail {
  color: #666666;
  font-size: 13px;
}
.notes_title {
  color: #999999;
}
.notes {
  color: #666666;
}
.notes a {
  color: #169bd5;
}
.cms-form-layout {
  padding: 0;
}
.cms-batch {
  border-top: 0px solid #f0f0f0 !important;
}
</style>




