<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" v-if="status" ref='productTab'>
      <el-tab-pane label="关注后自动回复" name="first">
        <list></list>
      </el-tab-pane>
      <el-tab-pane label="默认回复" name="two" lazy>
        <defaultReply></defaultReply>
      </el-tab-pane>
      <el-tab-pane label="关键词回复" name="three" lazy>
        <keywordReplyList></keywordReplyList>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import list from './list'
import defaultReply from './defaultReply'
import keywordReplyList from './keywordReplyList'
export default {
  name: 'productList',
  components: {
    list,
    defaultReply,
    keywordReplyList
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {},
  activated() {
    if (this.$route.query.type === 'list') {
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    }
  }
}
</script>

<style scoped>

</style>




